import { useState } from "react";
import { Bar } from "react-chartjs-2";
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from "chart.js";
import Select from "react-select"; // For better multi-select UI

// Register Chart.js components
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const Statistics = ({ data }) => {
  const [selectedLocations, setSelectedLocations] = useState([]);

  // Extract unique locations and format for react-select
  const uniqueLocations = [...new Set(data.map((d) => d.Location))].map((loc) => ({ value: loc, label: loc }));

  // Filter data based on selected locations
  const filteredData =
    selectedLocations.length === 0
      ? data
      : data.filter((d) => selectedLocations.some((loc) => loc.value === d.Location));

  // Compute fraud type counts
  const offensesCount = filteredData.reduce((acc, { Type }) => {
    acc[Type] = (acc[Type] || 0) + 1;
    return acc;
  }, {});

  // Chart data
  const chartData = {
    labels: Object.keys(offensesCount),
    datasets: [
      {
        label: "Number of Cases",
        data: Object.values(offensesCount),
        backgroundColor: ["rgba(255, 99, 132, 0.6)", "rgba(54, 162, 235, 0.6)"],
      },
    ],
  };

  return (
    <div className="p-4 bg-white shadow-lg rounded-lg">
      <h3 className="text-lg font-bold mb-2">Fraud Statistics</h3>

      {/* Multi-Select Dropdown */}
      <Select
        isMulti
        options={uniqueLocations}
        value={selectedLocations}
        onChange={setSelectedLocations}
        placeholder="Select Locations..."
        className="mb-4"
      />

      {/* Render Chart */}
      {filteredData.length > 0 ? (
        <Bar data={chartData} />
      ) : (
        <p className="text-gray-600">No data available for the selected locations.</p>
      )}
    </div>
  );
};

export default Statistics;
