import { Link } from "react-router-dom";

const Navbar = () => (
  <nav className="p-4 bg-white shadow-lg flex justify-between items-center">
    <div>
      <Link to="/" className="px-4">
        Home
      </Link>
      <Link to="/about" className="px-4">
        About
      </Link>
    </div>
  </nav>
);

export default Navbar;
