import { useEffect, useState } from "react";
import {
  MapContainer,
  TileLayer,
  useMap,
  Marker,
  Tooltip,
} from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import "leaflet.heat";
import Button from "../atoms/Button";
import { locationMap } from "../../constants/locations";

// Custom icon for markers with transparent background
const createLabelIcon = (text) => {
  return L.divIcon({
    className: "fraud-label",
    html: `<div style="color: white; font-weight: bold; text-align: center; background: rgba(0,0,0,0.5); padding: 4px 16px 4px 10px; border-radius: 22px;">${text}</div>`,
  });
};

const HeatmapLayer = ({ points }) => {
  const map = useMap();

  useEffect(() => {
    if (points.length === 0) return;

    const heatLayer = L.heatLayer(
      points.map(({ lat, lng }) => [lat, lng, 0.7]), // Normalize intensity
      {
        radius: 25,
        gradient: {
          0.0: "yellow",
          0.25: "#FFAA33",
          0.5: "orange",
          0.75: "red",
          "1.0": "#DC143C",
        },
        blur: 0,
        maxZoom: 8,
      }
    );

    heatLayer.addTo(map);
    return () => map.removeLayer(heatLayer);
  }, [map, points]);

  return null;
};

const Map = ({ data }) => {
  const [showNumbers, setShowNumbers] = useState(true);
  // Compute fraud type counts
  const offensesCount = data.reduce((acc, { Location }) => {
    const loc = Location.trim().replace(/ /g,'')
    acc[loc] = (acc[loc] || 0) + 1;
    return acc;
  }, {});

  const toggleNumbers = () => {
    setShowNumbers(!showNumbers);
  }

  return (
    <>
    <Button onClick={toggleNumbers} text={`${showNumbers ? 'Hide' : 'Show'} stats`} />
      <MapContainer
        center={[20.5937, 78.9629]}
        zoom={5}
        className="h-96 w-full rounded-lg"
      >
        <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
        {data.length > 0 && <HeatmapLayer points={data} />}
        {/* Add Labels to Show Fraud Numbers */}
        {Object.entries(offensesCount).map(([ location , cases ], index) => {
          const { lat, lng } = locationMap[location];
          return <Marker
            key={index}
            position={[lat, lng]}
            icon={createLabelIcon(cases)}
            opacity={showNumbers ? 1 : 0}
          >
            <Tooltip direction="top" offset={[0, -10]} opacity={1}>
              {cases} cases
            </Tooltip>
          </Marker>
        })}
      </MapContainer>
      </>
  );
};

export default Map;
