import { useState, useEffect } from "react";
import Papa from "papaparse";
import Heading from "../components/atoms/Heading";
import Map from "../components/organisms/Map";
import Card from "../components/molecules/Card";
import Statistics from "../components/organisms/Statistics";
import { locationMap } from "../constants/locations";

const Home = () => {
  const [fraudData, setFraudData] = useState([]);

  useEffect(() => {
    const fetchCSVData = async () => {
      const response = await fetch("/Cases.csv"); // Fetch from public folder
      const reader = await response.text();
      Papa.parse(reader, {
        header: true,
        skipEmptyLines: true,
        complete: (result) => {
          const data = result.data
            .map((row) => {
              const location = locationMap[row.Location?.trim().replace(/ /g,'')];
              return location
                ? {
                    ...row,
                    lat: location.lat,
                    lng: location.lng,
                  }
                : null;
            })
            .filter(Boolean);
          setFraudData(data);
        },
      });
    };

    fetchCSVData();
  }, []);

  return (
    <div className="space-y-6">
      <Heading text="Invisible Justice: Mapping Fraud against Qeer Men and Trans Individuals" />
      {/* <p className="text-gray-600">
        Invisible Justice: Mapping Fraud against Qeer Men and Trans Individuals
      </p> */}
      {fraudData.length > 0 ? (
        <>
        <Map data={fraudData} />
      <div className="grid grid-cols-2 gap-4">
        {/* <Card
          title="History"
          description="The LGBTQ+ movement in India has seen remarkable progress..."
        />
        <Card
          title="Current Issues"
          description="Despite decriminalization, discrimination persists..."
        /> */}
      </div>
          <Statistics data={fraudData} />
        </>
      ) : (
        <p>Loading data...</p>
      )}
    </div>
  );
};

export default Home;
